import { call } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";

export const closeAdditionDrower = createCommand<void>(
  'closeAdditionDrower',
  function* ({ payload }) {
    yield call(console.log, 'closeAdditionDrower', payload);
  }
);

export const startSubjectPageForStudent = createCommand<void>(
  'startSubjectPageForStudent',
  function* () {
    yield call(console.log, 'startSubjectPageForStudent');
  }
);
